import React from 'react';
import s from './NotFoundPage.module.css';
import SadSmile from '../../../assets/images/SadSmile.png'
import {useHistory} from "react-router-dom";

export const NotFoundPage: React.FC = () => {
    const history = useHistory()
    return (
        <div className={s.notFoundPage}>
            <div className={s.notFoundContainer}>
                <div className={s.notFoundBody}>
                    <img className={s.sadSmile} src={SadSmile}/>
                    <div className={s.errorName}>Oops! 404</div>
                    <div className={s.errorDescription}>
                    The requested URL <span className={s.notFoundPath}>{history.location.pathname}</span> was not found
                    on this server
                    </div>
                </div>
                <div className={s.notFoundDescription}>

                    There may be several reasons for this:

                    <ul className={s.notFoundList}>
                        <li>The page has been removed;</li>
                        <li>The page has been moved to a different location;</li>
                        <li>The page never existed and you were just given the wrong link.</li>
                    </ul>


                </div>

            </div>
        </div>
    )

}
